body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'AvenirNextLTPro-Regular';
  src: url('http://anthillrealtors.co.id/wp-content/themes/x-child/Fonts/AvenirNextLTPro-Regular.eot');
  src: url('http://anthillrealtors.co.id/wp-content/themes/x-child/Fonts/AvenirNextLTPro-Regular.eot?#iefix') format('embedded-opentype'),
  url('http://anthillrealtors.co.id/wp-content/themes/x-child/Fonts/AvenirNextLTPro-Regular.woff') format('woff'),
  url('http://anthillrealtors.co.id/wp-content/themes/x-child/Fonts/AvenirNextLTPro-Regular.ttf') format('truetype'),
  url('http://anthillrealtors.co.id/wp-content/themes/x-child/Fonts/AvenirNextLTPro-Regular.svg#AvenirNextLTPro-Regular') format('svg');
}

:root {
  --blueColor: #213F7D;
  --greenColor: #39CDCC;
  --background: #E5E5E5;
  --boxShadow: 0px 15px 90px rgba(0, 0, 0, 0.03);
  --fontFamily: 'AvenirNextLTPro-Regular';
  --mainTransition: 0.4s all ease;
  --textColor: #545F7D;
}
body{
  font-family: 'AvenirNextLTPro-Regular';
  font-family: var(--fontFamily);
  margin: 0;
  padding: 0;
  background: #E5E5E5;
  background: var(--background);
}

.logo1{
  width: 24.75px;
  height: 25px;
  position: relative;
  top: -7px;
}
.logo2{
  width: 138.44px;
  height: 36px;
  padding-left: 9px;
}
.loginPage{
    padding: 2rem 3rem;
    position: relative;
}
.login-row-session{
    display: flex;
    grid-gap: 8rem;
    gap: 8rem;
    padding: 70px 0px;
}
.login-row-image{
    flex: 1 1;
}
.login-row-image img{
    width: 600px;
    height: 337.57px;
}
.login-container{
    flex: 1 1;
}
.login-container .Welcome{
    font-family: var(--fontFamily) ;
    font-weight: bold;
    font-size: 40px;
    color: var(--blueColor);
    line-height: 50px;
    letter-spacing: -0.04em;
}
.login-container .text{
    font-family: 'Avenir Next';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: #545F7D;
    padding-bottom: 25px;
}
.login-form p{
    color: rgba(240, 96, 96, 0.726);
    font-size: 13px;
    position: relative;
    top:-5px;
    font-style: italic;
    margin-bottom: 20px;
}
.form-input{
    border:1px solid rgba(84, 95, 125, 0.15);
    border-radius: 5px;
    padding: 0px 10px;
    width: 60%;
    height: 45px;
    font-size: 16px;
    color: var(--textColor)
}
.form-input:focus{
    outline: none;
    border: 1px solid var(--greenColor)
}
.passwordshow{
    position: relative;
}
.show-password{
    position: relative;
    left:-50px;
    border: none;
    text-transform: uppercase;
    font-size: 13px;
    background: transparent;
    color: var(--greenColor);
    cursor: pointer;
    font-weight: 600;
    transition: var(--mainTransition);
}
.show-password:hover{
    color: var(--blueColor)
}
.forgot-password{
    text-transform: uppercase;
    color: var(--greenColor);
    font-size: 14px;
    font-weight: 700;
    padding-bottom: 20px;
    cursor: pointer;
    transition: var(--mainTransition)
}
.forgot-password:hover{
    color: var(--blueColor)
}
.submit-btn{
    width: 60%;
    text-align: center;
    background: var(--greenColor);
    color: #fff;
    text-transform: uppercase;
    padding: 10px;
    border-radius: 8px;
    border: none;
    cursor: pointer;
    font-weight: 700;
    transition: var(--mainTransition);
}
.submit-btn:hover{
    background: var(--blueColor);
}
.button-text{
    border: none;
    cursor: pointer;
    margin-top: 20px;
    color: var(--textColor);
    background: transparent;
}

@media screen and (max-width: 500px) {
    .login-row-session{
        display: flex;
        flex-direction: column;
    }
}
.interface-container{
    background: var(--background);
    width: 100%!important;
    position: relative;
}
.header{
    position:fixed;
    background:#fff;
    padding: 0 2rem ;
    width: 100%;
    height:70px;
    box-shadow: 3px 0px 20px rgba(0, 0, 0, 0.04);
    z-index:100;
    display: flex;
    justify-content: space-between;
}
.nav-logo{
    display: flex;
    flex: 2 1;
    padding-top: 1rem;
}
.headerlabel, .search{
    padding-top: 6px;
    padding-left: 20px;
}
.search{
    margin-left: 10rem;
    display: flex;
    height: 30px;
    width: 250px;
    position: relative;
}
.nav-input{
    width: 100%;
    border-radius: 5px;
    border:  1px solid var(--blueColor);
    padding-left: 5px;
}
.nav-input:focus{
    outline: none;
    border: 1px solid var(--greenColor)
}
.search span{
    background: var(--greenColor);
    position: absolute;
    right: 0px;
    height:30px;
    width: 35px;
    border-bottom-right-radius: 5px;
    border-top-right-radius: 5px;
    transition: var(--mainTransition);
}
.search span:hover{
    background: var(--blueColor);
}
.search span img{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    cursor: pointer;
}
.nav-user{
    flex: 1 1;
    display: flex;
    justify-content: space-around;
    margin-right: 3rem;
    padding-top: 20px;
}
.nav-text{
    text-decoration: underline;
    color: var(--textColor);
    cursor: pointer;
}
.nav-user img{
    width: 20px;
    height: 20px
}
.nav-user .user-profile{
    display: flex;
}
.user-profile span{
    width: 43px;
    height: 43px;
    border-radius: 50%;
    padding: 5px;
    background: var(--background);
    position: relative;
    top:-16px;
    margin-right: 10px;
}
.user-profile img{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    position: absolute;
    top: 50%;
    left:50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.user-profile  .nav-name{
    color: var(--textColor);
    text-transform: capitalize;
    font-size: 15px;
}
.sidebar{
    background:#fff;
    margin-top: 4rem;
    padding-top:10px;
    position: fixed;
    left: 0;
    width: 230px;
    height:calc(100% - 20%);
    overflow-y:auto;
    transition: 0.4s ;
    transition-property: left;
    z-index:100;
    padding-bottom: 30px;
}
.sidebar ul{
    list-style: none;
    margin:0;
    padding:0;
}
.sidebar ul li{
    list-style-type: none;
}

.sidebar li .sidebar-navlink{
    color:var(--blueColor);
    display:block;
    width:100%;
    line-height: 45px;
    text-decoration: none;
    padding-left: 20px;
    box-sizing: border-box;
    transition: 0.5s all ease; 
    font-family: 'Work Sans', sans-serif;
    font-size: 16px; 
}
.sidebar li .sidebar-navlink:hover, .active{
    background:var(--greenColor);
}
.sidebar li .sidebar-icons{
    padding-right: 13px;
    font-size: 14px;
}
.sidebar li p{
    padding-left: 20px;
    color: var(--blueColor);
    font-size: 15px;
    font-weight: 600;
}
#check:checked ~ .sidebar{
    left: -190px;
}
#check:checked ~ .sidebar .sidebar-navlink span{
    display:none;
}
#check:checked ~ .sidebar .sidebar-navlink{
    font-size: 20px;
    margin-left: 170px;
    width: 80px;
}
  
.content{
    margin-left:250px;
    background-position: center;
    background-size: cover;
    height: auto;
    transition: 0.4s all ease;
    padding-top: 5%;
    padding-left: 5px;
    background: var(--background);
}
#check:checked ~ .content{
    margin-left: 60px;
}
#check{
    display: none;
}

@media screen and (max-width: 768px) {
    #check:checked ~ .content{
        margin-left: 20px;
    }
    #check:checked ~ .sidebar .sidebar-navlink{
        font-size: 20px;
        margin-left: 0px!important;
        width: 0px;
    }
}
.details-container{
    display: flex;
    flex-direction: column;
    grid-gap: 2rem;
    gap: 2rem;
    padding:2rem;
    font-family: 'Work Sans';
    width: 90%;
}
.top-div{
    cursor: pointer;
}
.top-div span{
    padding-left: 15px;
}
.top-div .back{
    background: none;
    color: var(--textColor);
    text-decoration: none;
    font-size: 15px;
    transition: var(--mainTransition);
}
.top-div:hover .back{
    color: var(--greenColor)
}
.userdetails{
    display: flex;
    justify-content: space-between;
}
.userdetails .text{
    color: var(--blueColor);
    font-size: 23px;
    text-transform: capitalize;
}
.userdetails .btn>:nth-child(1){
    color: #E4033B;
    letter-spacing: 0.1em;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 600;
    padding: 10px;
    border: 1px solid#E4033B;
    border-radius: 4px;
    cursor: pointer;
    transition: var(--mainTransition);
}
.userdetails .btn>:nth-child(1):hover{
    background: #E4033B;
    color:#fff;
}
.userdetails .btn>:nth-child(2){
    color: var(--greenColor);
    letter-spacing: 0.1em;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 600;
    padding: 10px;
    border: 1px solid var(--greenColor);
    border-radius: 4px;
    margin-left: 20px;
    cursor: pointer;
    transition: var(--mainTransition)
}
.userdetails .btn>:nth-child(2):hover{
    background: var(--greenColor);
    color:#fff;
}
.detailuser-profile{
    background: #fff;
    border-radius: 5px;
}
.detailuser-profile .top{
    display: flex;
    grid-gap: 3rem;
    gap: 3rem;
    padding: 1rem;
}
.detail-avatar{
    border-radius: 50%;
    height: 80px;
    width: 80px;
}
.detailname>:nth-child(1){
    color: var(--blueColor);
    font-size: 22px;
    text-transform: capitalize;
}
.detailname>:nth-child(2){
    color: var(--textColor);
    position: relative;
    top:-15px;
    font-size: 15px;
}
.user-tiers p{
    color: var(--textColor);
    font-size: 14px;
    font-weight: 600;
    padding-top: 15px;
}
.user-tiers img{
    position: relative;
    top:-5px;
}
.money>:nth-child(1){
    color: var(--blueColor);
    font-size: 23px;
}
.money>:nth-child(2){
    color: var(--blueColor);
    font-size: 14px;
    position: relative;
    top:-15px;
}

/* css for tabs here */
.bloc-tabs {
    display: flex;
    grid-gap: 5rem;
    gap: 5rem;
    padding: 0 1rem 1rem 1rem;
}
.tabs {
    background: var(--backgroundColor);
    cursor: pointer;
    box-sizing: content-box;
    position: relative;
    outline: none;
    transition: 0.4s all ease;
    color: var(--textColor);
    font-size: 15px;
    font-weight: bold;
    text-align: center;
}
.active-tabs  {
    border-bottom: 1px solid var(--greenColor);
    color:var(--greenColor);
}
